:root {
  --font-family: "Montserrat", sans-serif;
  --min-width: 320px;
  --max-width-container: 1590px;
  --md-xl: 1200px;
  --md-l: 992px;
  --md-m: 768px;
  --md-s: 480px;
  --main-red: #f9423a;
  --main-dark-blue: #121239;
  --input-bg-color:#2B2B56;
  --main-violet: #7949f4;
  --main-blue: #002fa7;
  --main-white: #fff;
  --plus-yellow: #f9c546;
  --plus-green: #71f270;
  --btn-green: #7ae28f;
  --plus-light-blue: #58c0ed;
  --plus-purple: #c252f7;
  --plus-2-gray: #f5f5f5;
  --plus-3-gray: #cdcfdd;
  --plus-4-gray: #8c8d9d;
  --plus-5-gray: #737383;
  --plus-gray-text-6: #4d4d6a;
  --plus-bg: #f5f3f9;
  --plus-bg-light: #d8dfe2;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
  cursor: none !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:focus,
button:focus {
  outline: 2px solid var(--main-violet);
}

a {
  color: inherit;
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: var(--min-width);
}

body {
  font-size: 16px;
  line-height: 1;
  font-family: var(--font-family);
  color: var(--main-white);
  font-weight: 400;
  font-style: normal;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: var(--font-family);
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}
.main__home {
  padding: 120px 0 80px 0;
}
@media (max-width: 480px) {
  .main__home {
    padding: 100px 0 40px 0;
  }
}
.main__benefits, .main__contact-us, .main__expertise {
  padding: 140px 0;
}
@media (max-width: 992px) {
  .main__benefits, .main__contact-us, .main__expertise {
    padding: 80px 0;
  }
}
@media (max-width: 480px) {
  .main__benefits, .main__contact-us, .main__expertise {
    padding: 40px 0;
  }
}
@media (max-width: 992px) {
  .main__automation {
    padding: 80px 0;
  }
}
@media (max-width: 480px) {
  .main__automation {
    padding: 40px 0;
  }
}
.main__solutions {
  padding: 150px 0 166px 0;
}
@media (max-width: 992px) {
  .main__solutions {
    padding: 80px 0;
  }
}
@media (max-width: 480px) {
  .main__solutions {
    padding: 40px 0;
  }
}
.container {
  max-width: var(--max-width-container);
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: var(--md3)) {
  .container {
    padding: 0 10px;
  }
}

.title {
  font-size: 40px;
  font-weight: 700;
  line-height: 130%;
  letter-spacing: -0.8px;
  text-transform: uppercase;
  color: var(--main-white);
}
@media (max-width: 768px) {
  .title {
    font-size: 32px;
  }
}
@media (max-width: 480px) {
  .title {
    font-size: 30px;
  }
}
.title__color_purple {
  color: var(--plus-purple);
}
.title__color_green {
  color: var(--plus-green);
}
.title__color_white {
  color: var(--main-white);
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: var(--main-white);
  letter-spacing: -0.16px;
  border-radius: 10px;
  padding: 10px 20px;
  background-color: var(--btn-green);
  border: 1px solid var(--btn-green);
  transition: color 0.3s ease, background-color 0.3s ease, border-color 0.3s ease;
}
@media (any-hover: hover) {
  .btn:hover {
    background-color: var(--main-white);
    color: var(--main-violet);
    border-color: var(--main-violet);
  }
}

.logo__img {
  flex: 0 0 45px;
  height: 45px;
  margin-right: 10px;
}
.logo__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.logo__text span {
  display: block;
}
.logo__text span:first-child {
  font-size: 28px;
  font-weight: 700;
}
.logo__text span:last-child {
  font-size: 18px;
}

.hidden {
  overflow: hidden;
}

.text {
  font-size: 20px;
  line-height: 140%;
  letter-spacing: -0.2px;
}
@media (max-width: 480px), (max-height: 650px) {
  .text {
    font-size: 16px;
    line-height: 120%;
  }
}

.form-label {
  display: block;
}

.form-input,
.form-textarea {
  display: block;
  width: 100%;
  height: 100%;
  padding: 20px 30px;
  color: var(--plus-5-gray);
  letter-spacing: -0.16px;
  outline: none;
  border: 2px solid var(--main-white);
  transition: border-color 0.3s ease;
}
.form-input:focus,
.form-textarea:focus {
  border-color: var(--main-violet);
}
.form-input::placeholder,
.form-textarea::placeholder {
  color: var(--plus-5-gray);
  letter-spacing: -0.16px;
}
@media (max-width: 480px) {
  .form-input,
.form-textarea {
    padding: 10px;
  }
}

.form-textarea {
  resize: none;
}

.checkbox {
  appearance: none;
}
.checkbox:checked + .checkbox-tag::after {
  transform: scale(1);
}
.checkbox:focus-visible + .checkbox-tag {
  border-color: var(--main-violet);
}
.checkbox input::-webkit-outer-spin-button,
.checkbox input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox-tag {
  position: relative;
  display: inline-block;
  color: var(--main-dark-blue);
  font-size: 14px;
  padding-left: 25px;
  padding-bottom: 5px;
  border-bottom: 2px solid transparent;
  transition: border-color 0.3s ease;
}
.checkbox-tag::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  border: 2px solid var(--main-violet);
}
.checkbox-tag::after {
  transition: all 0.3 ease 0s;
  position: absolute;
  content: "";
  top: 3px;
  left: 3px;
  width: 9px;
  height: 9px;
  background-color: var(--main-violet);
  transform: scale(0);
}

.link-hover {
  position: relative;
}
.link-hover::before {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 2px;
  transition: width 0.5s ease;
}
@media (any-hover: hover) {
  .link-hover:hover::before {
    width: 100%;
  }
}

.mouse {
  position: fixed;
  top: 0;
  left: 0;
  width: 25px;
  height: 25px;
  border-radius: 100%;
  z-index: 10000000;
  pointer-events: none;
}
.mouse::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 25px;
  height: 25px;
  border-radius: 100%;
  background-color: var(--plus-green);
  z-index: 2;
  transition: opacity 0.3s ease;
}
.mouse::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 35px;
  height: 35px;
  border-radius: 100%;
  background-color: var(--btn-green);
  transition: opacity 0.3s ease;
}
.mouse__view {
  position: absolute;
  left: -100%;
  top: -100%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  font-family: "DrukCyr";
  font-size: 22px;
  font-weight: 500;
  z-index: 500;
  text-transform: uppercase;
  color: var(--main-dark-blue);
  background-color: var(--btn-green);
  transform: rotate(-45deg);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.view-visible {
  opacity: 1;
  transition: opacity 0.3s ease;
}

.links-visible::after {
  border: 4px solid var(--plus-green);
  transform: translate(-50%, -50%) scale(0.7);
  background-color: transparent;
  transition: 0.3s;
}

.links-visible::before {
  opacity: 0;
}

