.header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 500;
  transition: box-shadow 0.3s ease;
  backdrop-filter: blur(15px);
}
.header.active {
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.8);
  background-color: rgba(18, 18, 57, 0.5);
}
.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 90px;
  width: 100%;
  padding: 10px 0;
}
@media (max-width: 480px) {
  .header__body {
    min-height: 70px;
  }
}
.header__logo {
  flex: 0 0 176px;
  margin-right: 15px;
  display: flex;
  align-items: center;
}
.header__menu {
  margin-right: 15px;
}
@media (max-width: 1300px) {
  .header__menu {
    position: fixed;
    top: 0;
    left: 0;
    transform: translateY(-110%);
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    z-index: 10;
    background-color: var(--main-dark-blue);
    transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease;
    padding: 70px 0;
  }
  .header__menu.active {
    transform: translateY(0);
    transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease;
  }
}
.header__list {
  height: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 1300px) {
  .header__list {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
  }
}
@media (max-width: 480px) {
  .header__list {
    height: auto;
    margin-bottom: 50px;
  }
}
.header__item:not(.header__item:last-child) {
  margin-right: 5px;
}
@media (max-width: 1300px) {
  .header__item:not(.header__item:last-child) {
    margin: 0;
    margin-bottom: 15px;
  }
}
.header__item_submenu {
  position: relative;
}
.header__item_submenu.active .header__submenu {
  opacity: 1;
  visibility: visible;
}
.header__item_submenu.active .header__link_submenu span {
  transform: rotate(-180deg) translateX(-10px);
}
.header__link {
  padding: 10px;
  font-size: 13px;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .header__link:hover {
    color: var(--plus-green);
  }
}
@media (max-width: 1300px) {
  .header__link {
    font-weight: 700;
    padding: 0;
    font-size: 3vh;
  }
}
.header__link_submenu {
  display: flex;
  align-items: center;
  font-size: 13px;
  color: var(--main-white);
}
.header__link_submenu span {
  display: block;
  padding-left: 10px;
  font-size: 8px;
  transition: transform 0.3s ease;
}
@media (max-width: 1300px) {
  .header__link_submenu {
    display: none;
  }
}
.header__submenu {
  opacity: 0;
  visibility: hidden;
  width: 570px;
  min-height: 200px;
  padding: 30px 50px;
  position: absolute;
  bottom: -226px;
  left: 0;
  display: flex;
  background-color: var(--main-violet);
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
@media (max-width: 1300px) {
  .header__submenu {
    position: static;
    opacity: 1;
    visibility: visible;
    background-color: var(--main-dark-blue);
    flex-direction: column-reverse;
    align-items: center;
    width: 100%;
    padding: 0;
  }
}
.header__menu-actions {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 480px) {
  .header__menu-actions {
    flex-direction: column;
  }
}
.header__actions {
  display: flex;
  align-items: center;
  flex: 0 0 340px;
}
@media (max-width: 1300px) {
  .header__actions {
    flex: 0 0 auto;
  }
}

.submenu-header__sublist:not(.submenu-header__sublist:last-child) {
  margin-right: 60px;
}
@media (max-width: 1300px) {
  .submenu-header__sublist {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .submenu-header__sublist:not(.submenu-header__sublist:last-child) {
    margin-right: 0;
  }
  .submenu-header__sublist:not(.submenu-header__sublist:first-child) {
    margin-bottom: 15px;
  }
}
.submenu-header__subitem:not(.submenu-header__subitem:last-child) {
