}

.actions-header__phone {
  margin-right: 20px;
  font-size: 16px;
  letter-spacing: -0.16px;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .actions-header__phone:hover {
    color: var(--plus-green);
  }
}
@media (max-width: 480px) {
  .actions-header__phone {
    margin-bottom: 20px;
  }
}
.actions-header__callback {
  backdrop-filter: blur(0px);
}
.actions-header__burger {
  display: none;
}
@media (max-width: 1300px) {
  .actions-header__burger {
    display: block;
    position: relative;
    width: 40px;
    height: 18px;
    cursor: pointer;
    z-index: 11;
    margin: 0 20px;
    transition: background-color 0.3s ease 0s;
  }
  .actions-header__burger span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: var(--main-white);
  }
  .actions-header__burger span:first-child {
    top: 0;
  }
  .actions-header__burger span:last-child {
    top: auto;
    bottom: 0;
  }
  .actions-header__burger.active span {
    transform: scale(0);
  }
  .actions-header__burger.active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }
  .actions-header__burger.active span:last-child {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
  }
}
@media (max-width: 1300px) and (any-hover: hover) {
  .actions-header__burger:hover span {
    background-color: var(--plus-green);
  }
}
@media (max-width: 1300px) and (max-width: 480px) {
  .actions-header__burger {
    margin-left: 15px;
  }
}

