.footer__bottom {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
}
@media (max-width: 480px) {
  .footer__bottom {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

.top-footer__logo {
  display: inline-flex;
  align-items: center;
  margin-bottom: 30px;
}

.center-footer__title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 15px;
}
.center-footer__address {
  margin-bottom: 15px;
}
.center-footer__item:not(.center-footer__item:last-child) {
  margin-bottom: 10px;
}
.center-footer__link {
  display: inline-flex;
}
.center-footer__link::before {
  color: var(--plus-green);
  padding-right: 10px;
  transition: padding 0.3s ease;
}
@media (any-hover: hover) {
  .center-footer__link:hover::before {
    padding-right: 20px;
  }
}

.bottom-footer__link {
  color: var(--plus-green);
}
.bottom-footer__link::before {
  background-color: var(--plus-green);
}